import React from "react";
import { AboutUs } from "./Us.style";
import datas from "./data";

const index = () => {
  return (
    <section id="us">
      <AboutUs>
        <ul>
          {datas.map((data, index) => (
            <li key={index}>
              <data.icon color={data.color} />
              <p>{data.text}</p>
            </li>
          ))}
        </ul>
      </AboutUs>
    </section>
  );
};

export default index;
